/*
  GLOBAL VARIABLES
*/
/*
  MIXINS
*/
body {
  padding: 30px 30px 40px 30px; }
  body * {
    font-family: "Source Sans Pro", sans-serif; }

/*
  EXERCISE-APP
*/
#exercise_app {
  /*
    Make Responsive
  */
  padding: 20px;
  border: 2px solid #0B2544;
  border-radius: 10px;
  height: 500px;
  min-width: 900px; }
  #exercise_app ol, #exercise_app ul {
    padding: 0;
    margin: 0;
    list-style-type: none; }
  #exercise_app .column_container {
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between; }
  #exercise_app .column {
    border: 1px solid #000;
    padding: 20px 20px 30px 20px;
    background-color: #F9F9F7; }

/*
  LEFT-COLUMN
*/
.left_column {
  width: 321px;
  overflow-y: hidden;
  margin-right: 20px;
  height: calc(100% - 50px);
  position: relative; }
  .left_column .search_container {
    background-color: #F9F9F7; }
    .left_column .search_container input {
      width: calc(100% - 16px);
      padding: 10px 0 10px 15px;
      border-radius: 4px;
      border: 1px solid #A0A0A0;
      margin-bottom: 20px; }
  .left_column .list_container {
    height: 390px;
    overflow: auto;
    border: 1px solid #A0A0A0; }
  .left_column li {
    border-top: 1px solid #A0A0A0;
    border-bottom: 0px;
    padding: 12px;
    background-color: #FFF; }
    .left_column li:first-child {
      border-top: 0px; }
    .left_column li:last-child {
      border-bottom: 1px solid #A0A0A0; }
    .left_column li:hover {
      cursor: pointer;
      background-color: #0B2544;
      color: #FFF; }
  .left_column .active {
    background-color: #0B2544;
    color: #FFF; }

/*
  RIGHT-COLUMN
*/
.right_column {
  flex: 1;
  overflow: hidden; }
  .right_column h2, .right_column h3 {
    text-align: center;
    text-transform: uppercase;
    font-size: 28px;
    font-weight: bold;
    padding: 0;
    margin: 0; }
  .right_column p {
    text-align: center;
    font-size: 16px; }

/*
  VIDEO
*/
.video_container {
  margin: 20px auto;
  width: 300px;
  height: 300px;
  border: 2px solid #0B2544; }
  .video_container video {
    width: 100%; }
